import React, { Fragment } from 'react';
import './styles.less';
import no_bg from './no_bg.png';

/**
 * 数字组件
 * num 需要显示的数字
 * type 默认只显示数字，当为 bgText 时，显示数字背景图片
 */

export default ({ num, type }) => {
    if(Number(num) || Number(num) === 0){
        if(!type){
            return (
                <Fragment>
                    {(num+'').split("").map((item,i)=>{
                        // if(parseInt(item)===1){
                        //     return (<i key={i} style={{display:"inline-block",width:10}} className={`iconfont icon-shuzi${item}`}></i>);
                        // }
                        if(item==="."){
                            return <span key={i} className='point'>{item}</span>;
                        }
                        return (<i key={i}>{item}</i>);
                    })}
                </Fragment>
            )
        } else if(type==="bgText"){
            return (
                <Fragment>
                    {(num+'').split("").map((item,i)=>{
                        // if(parseInt(item)===1){
                        //     return (<i key={i} style={{display:"inline-block",width:10}} className={`iconfont icon-shuzi${item}`}></i>);
                        // }
                        if(item==="."){
                            return <span key={i} className='point'>{item}</span>;
                        }
                        return (
                            <span key={i} className='icon-number-bg number' style={{backgroundImage: `url(${no_bg})`}}>
                                <i>{item}</i>
                            </span>
                        );
                    })}
                </Fragment>
            )
        }

    } else {
        return (
            <Fragment></Fragment>
        )
    }
}
