import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './index.less';
import NumberComp from './NumberComp';
import NowTime from './NowTime';
import dataJson from './欢迎页-数据.json';

class IndexPage extends Component {
  state = {
    dataInfo: {
      teacherDevelopment: "86.2",
      schoolLevel: "85.3",
      studentDevelopment: "82.9",
      managementService: "87.6"
    }
  }
  componentDidMount(){
    this.setState({
      dataInfo: dataJson.RECORDS[0]
    })
  }
  render () {
    let dataInfo = this.state.dataInfo;
    return (
      <div className='container' onClick={()=>{
        this.props.history.push("/dabiao");
      }}>
        <h2 style={{position: 'absolute', top: '40vh', left: '50%', transform: 'translateX(-50%)', color: '#25d5fc', fontSize: '48px'}}>质量体系诊断与改进平台</h2>
        {/* 办学水平 */}
        <div className='schooleLevel'>
          <NumberComp num={dataInfo["办学水平"]}/>
        </div>
        {/* 教师发展 */}
        <div className='teacherDevelop'>
          <NumberComp num={dataInfo["教师发展"]}/>
        </div>
        {/* 学生成长 */}
        <div className='studentGrowth'>
          <NumberComp num={dataInfo["学生成长"]}/>
        </div>
        <div className='centerNowTime' onClick={(e)=>{
          e.preventDefault();
          e.stopPropagation();
          this.props.history.push("/dabiao");
        }}>
          <NowTime/>
        </div>
      </div>
    );
  }
}

IndexPage.propTypes = {
};

export default withRouter(IndexPage);
